* {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App{
  max-width: 1550px;
  width: 100%;
  min-height: 100vh;
  margin: 0 auto;
  box-shadow: 0 5px 6px #08a05ca4;
  padding: 0 15px;
  box-sizing: border-box;
}
/*============ HEADER ===============*/
header{
  width: 97%;
  min-width: 100px;
  max-width: 1530px;
  overflow: hidden;
  display: flex;
  justify-content: space-around;
  align-items: center;
  flex-wrap: wrap;
  position: fixed;
  box-sizing: border-box;
  background-color: white;
  box-shadow: 0 3px 6px;
}

a{
  color: black;
  text-decoration: none;
  text-transform: uppercase;
}

a:hover{
  color: #08a05c;
}

li{
  list-style: none;
}

header ul{
  display: flex;
  padding: 0;
  margin: 0;
}

header ul li{
  padding: 0 1rem;
}

header ul li a{
  font-weight: bold;
}

header .cart {
  position: relative;
  cursor: pointer;
}



header .cart span{
  position: absolute;
  top: -1rem;
  right: -1.5rem;
  width: 1.5rem;
  height: 1.5rem;
  border: 1px solid red;
  border-radius: 50%;
  display: flex;
  align-items: center;
  justify-content: center;
  color: white;
  padding: 0rem;
  font-size: 1rem;
  font-weight: 700;
  background-color: crimson;
}
/*============ Inicio ===============*/
.inicio{
  width: 100%;
  margin: 0 auto;
  padding-top: 9rem;
}

.inicio h1{
  font-size: 2rem;
  color: #08a05c;
}

.inicio img{
  width: 100%;
}

/*============ PRODUCTO ===============*/
.title{
  font-size: 2.4rem;
  padding: 10rem 0 0 0;
  color: #08a05c;
}

.productos{
  padding: 5rem 0;
  margin: 0 auto;
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(22rem, 1fr));
  gap: 3rem 2rem;
  box-sizing: border-box;
}

.producto{
  display: flex;
  flex-direction: column;
  width: 100%;
  align-items: center;
  box-shadow: 1px 10px 10px 2px rgba(0, 0, 0, .2);
  border: 6px solid transparent;
  border-radius: 5px;
}

.producto:hover{
  border: 6px solid #08a05c
}

.producto__img img{
  width: 310px;
}

.producto__footer{
  padding: 1rem;
}

.producto__footer h1{
  font-weight: 600;
  font-size: 1.5rem;
  color: #1a202c;
  font-family: Helvetica, sans-serif;
}

.producto__footer p{
  font-weight: 700;
  color: rgba(0, 0, 0, .555);
  text-transform: uppercase;
  font-size: 18px;
}

.producto__footer .price{
  font-size: 2rem;
  color: #1a202c;
}

.producto .buttom{
  width: 100%;
  height: 45px;
  display: grid;
  grid-template-columns: 2fr 1fr;
  gap: 1rem 3rem;
}
.buttom .btn{
  border: none;
  outline: 0;
  cursor: pointer;
  border-radius: 5px;
}

.buttom button{
  background-color: #3033d3;
  max-width: 250px;
  width: 100%;
  
  font-size: 1.5rem;
  color: white;
  font-weight: 600;
}

.buttom div{
  background-color: #08a05c;
  display: grid;
  align-items: center;
  text-align: center;
  margin-left: -2rem;
  border-radius: 5px;
}

.buttom div a{
  color: white;
  font-weight: 700;
}

.buttom div a{
  color: white;
  font-weight: 700;
}
.buttom div a:hover{
  color: white;
}
/*============ RESPONSIVE ===============*/

@media only screen and (max-width: 600px){
  header ul{
    display: none;
  }
}